.services__brands {
  margin-bottom: 24px;

  @media (min-width: $breakpoint_sm) {
      margin-bottom: 32px;
    }

    @media (min-width: $breakpoint_md) {
      margin-bottom: 50px;
    }
}

.brands {
  &__title-wrapper {
    @extend %section-title-wrapper;
  }

  &__title {
    @extend %section-title;
  }

  &__swiper {
    position: relative;
    margin-right: -16px;

    @media (min-width: $breakpoint_sm) {
      margin-right: 0;
      margin-bottom: 24px;
    }

    &::after {
      position: absolute;
      content: "";
      top: 0;
      right: 0;
      display: block;
      background-image: linear-gradient(
        -90deg,
        #f8f8f8 16.84%,
        rgba(248, 248, 248, 0.0001) 100%
      );
      width: 24px;
      height: 81px;
      z-index: 2;

      @media (min-width: $breakpoint_sm) {
        content: none;
      }
    }
  }

  &__list {
    @extend %swiper-list;
  }

  &__item {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-basis: 240px;
    height: 72px;
    padding: 0 16px;
    border-radius: 6px;
    border: 1px solid $fourth-bg;
    background-color: $second-bg;

    @media (min-width: $breakpoint_sm) {
      flex-basis: 224px;
    }

    @media (min-width: $breakpoint_md) {
      flex-basis: 240px;
    }
  }

  &__wrapper-logo {
    display: flex;
    align-items: center;
  }

  &__button {
    @extend %section-button
  }

}

