.page__aside {
  transform: translateX(-100%);
  transform-origin: 0 50%;
  padding: 18px 16px 32px;


   @media (min-width: $breakpoint_xl) {
  padding: 30px 31px 0px 24px;
  }
}

.aside {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
      min-width: 320px;
  max-width: 360px;

    z-index: 2;
  font-family: $default-font;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: $main-color;
  background-color: $second-bg;
  transition: transform 0.4s ease-in-out, filter, 0.34s ease-in-out;

  @media (min-width: $breakpoint_xs) {
    & {
      max-width: 320px;
    }
  }

  @media (min-width: $breakpoint_xl) {
    position: static;
    transform: translateX(0);
  }

  &__wrapper {
  max-height: 887px;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  }

  &--active {
    transform: translateX(0);
    filter: drop-shadow(16px 0px 32px $aside-shadow-bg);
    height: min-content;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 22px;

    @media (min-width: $breakpoint_sm) {
    margin-bottom: 48px;
    }
    @media (min-width: $breakpoint_xl) {
    margin-bottom: 36px;
    }
  }

  &__button-close {
    margin-right: 16px;

    @media (min-width: $breakpoint_xl) {
      display: none;
    }
  }

  &__footer {
    margin-top: auto;
  }

  &__contacts {
    display: flex;
    gap: 16px;
    margin-bottom: 16px;
  }

  &__communication {
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-bottom: 8px;

    @media (min-width: $breakpoint_xl) {
    margin-bottom: 41px;
    }
  }

  &__switch-language {
    display: flex;
    gap: 7px;
  }


}

.nav {
  &__list {
    display: flex;
    flex-direction: column;
    gap: 24px;
    margin-bottom: 32px;

    @media (min-width: $breakpoint_sm) {
    gap: 28px;
    }
  }

  &__button--active {
    color: $second-color;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: calc((24px - 32px) / 2);
      left: -20px;
      width: 8px;
      height: 32px;
      line-height: 24px;
      border-radius: 4px;
      background-color: $third-bg;

 @media (min-width: $breakpoint_xl) {
width: 4px;
border-radius: 2px;
  }
    }
  }
}

.communication {
  &__link--phone {
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;
  }
}

.switch-language {
  &__label {
    cursor: pointer;
    text-transform: uppercase;
    transition: color 0.2s ease-in-out;
  }

  &__button {
    appearance: none;
    position: absolute;
  }

  &__button:checked + &__label {
    color: $second-color;
  }
}
